@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Roboto Mono", monospace;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
}

button {
  padding: 10px 20px;
  border-radius: 15px;
  font-size: 12px;
  font-weight: 400;
  border: none;
  color: #fff;
  background-color: rgb(19, 80, 192);
  cursor: pointer;
}

.create_box {
  width: 100%;
}
.create_expense {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.create_expense label {
  display: inline-block;
  width: 100px;
}
.create_expense input {
  width: 200px;
  margin: 5px 0;
}
.create_expense .btn_box {
  display: flex;
  justify-content: space-around;
  width: 180px;
  margin: 15px 0;
}

.create_btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin-bottom: 20px;
}

.expense_item {
  width: 100%;
}
.expense_item .expense_item_desc {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.expense_filter {
  display: flex;
  width: 100%;
  height: 50px;
  justify-content: space-between;
  padding: 0 20px;
  background-color: rgb(165, 196, 253);
  align-items: center;
}

.expense_filter select {
  width: 70px;
  height: 25px;
  text-align: center;
}

.expense_cnt {
  width: 500px;
  height: auto;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(245, 245, 245);
  box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.322);
  border-radius: 20px;
}

